define('example/models/tag', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  /* global faker */

  var Tag = Ember['default'].Object.extend({
    name: null
  });

  Tag.reopenClass({

    generate: function generate() {
      return this.create({
        name: faker.company.bsAdjective()
      });
    }

  });

  exports['default'] = Tag;

});